import { Icon } from 'vant';
<template>
    <div id='box'>
      <div class="top">
        <div class="top_type"><van-icon name="passed" color="#0BEC6E"/><span>兑换成功</span></div>
        <div class="title">您此次获取的密码是</div>
        <div class="mima">
          <span v-for="(item,i) in password" :key="i">{{item}}</span>
        </div>
        <p style="color:#fff;text-align:center;font-size:11pt;">请按照说明输入密码,该密码从现在<br>开始1个小时内有效</p>
      </div>
      <div class="again" @click="again()">重新获取密码</div>
      <p style="color:#999;text-align:center;font-size:11pt;">如遇密码不可用，5分钟内<br>可重新免费申请新密码 或者
        <span style="color:#15BDDB;" @click="kefu()">联系客服</span>
      </p>
    </div>
</template>

<script>
import { Icon, Toast, Dialog } from 'vant';
import { getmima, kefu } from '@/axios/getData';
export default {
    components: {
      vanIcon: Icon
    },
    props: {

    },
    data() {
      return {
        password:[],    //  密码列表
        item:{},        //  用来重新获取密码
      };
    },
    created() {
      var url = window.location.href;
      if(url.search('key=')!=-1){
        var item = {};
        localStorage.setItem("openid",url.split('openid=')[1].split('&')[0]);
        item.key = url.split('key=')[1].split('&')[0];
        item.number = url.split('number=')[1].split('&')[0];
        item.order_sn = url.split('order_sn=')[1].split('&')[0];
        item.id = url.split('id=')[1].split('&')[0];
        this.password = url.split('password=')[1].split('&')[0].split('');
        this.item = item;
        // alert(item)
        return;
      }
      if(this.$route.query.password){
        var item = JSON.parse(this.$route.query.password)
        this.item = item
        this.password = item.password.split("")
        console.log(this.password,1111);
      }
    },
    methods: {
      again(){
        // Toast(this.$route.query)
        var data = {};
        var item = this.item
        data.id =item.id
        data.number =item.number
        data.key =item.key
        data.order_sn =item.order_sn
        Toast(data)
        getmima(data).then((res)=>{
          if(res.code==200){
            Toast('密码获取成功')
            this.password = res.data.code.split('')
            // console.log(res.data)
          } else {
            Toast(res.msg)
          }
        });
      },
      //  联系客服
      kefu(){
        var that = this;
        if(that.$store.state.kefu){
          Dialog.confirm({
            title: that.$store.state.kefu.title,
            message: that.$store.state.kefu.number
          }).then(() => {
            // 不加回报异常          
          }).catch(() =>{
            // 不加回报异常
          })
          return;
        }
        kefu().then(res =>{
          that.$store.state.kefu = res.data
          Dialog.confirm({
            title: res.data.title,
            message: res.data.number
          }).then(() => {
            // 不加回报异常          
          }).catch(() =>{
            // 不加回报异常
          })
        })
      },
      
    },
    computed: {

    },

    mounted() {

    },

};
</script>

<style scoped lang="scss">
#box{
  height:100vh;
  background: #F5F5F5;
  .top{
    position: relative;
    background: #000;
    padding-top:20pt;
    padding-bottom:30pt;
    .top_type{
      display: flex;
      align-items: center;
      text-align: center;
      justify-content: center;
      color:#0BEC6E;
      span{
        display: inline-block;
        margin-left:10pt;
      }
    }
    .title{
      font-family: 'PingFang SC Regular';
      margin-top:15pt;
      text-align: center;
      font-size: 16pt;
      color:#fff !important;
      font-weight: 500 !important;
    }
    .mima{
      color:#fff;
      display: flex;
      padding:20pt 30pt;
      justify-content: space-between;
      &>span{
        display: inline-block;
        border-radius: 6pt;
        // margin-right:10pt;
        text-align: center;
        padding:10pt;
        background: #15BDDB;
        font-size: 30pt;
      }
    }
  }
  .top::after{
    position: absolute;
    left:50%;
    transform: translateX(-50%);
    display: block;
    content:'';
    border:15pt solid;
    bottom:-1pt;
    border-color:transparent transparent #F5F5F5 transparent;
  }
}
/* 重新获取密码 */
.again{
  margin: 30pt auto;
  width:50%;
  border:1.5pt solid #15BDDB;
  color:#15BDDB;
  text-align: center;
  padding:15px 0;
  border-radius: 30pt;
  font-weight: 500;
  font-size: 14pt;
}
</style>



